.nav {
  background-color: rgba(11, 13, 17, 0.479); }
  .nav .navbar-form {
    margin-right: 0px; }

.navbar-overlay {
  background-image: url("../imgs/bg-well.jpg"); }

#dashboard input.search {
  border: 0 !important;
  border-bottom: 3px solid #294399 !important;
  border-radius: 0px !important;
  box-shadow: 0px !important; }

